@import url(https://fonts.googleapis.com/css2?family=Chelsea+Market&family=Original+Surfer&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Chelsea+Market&family=Original+Surfer&display=swap);
*{
  margin:0;
  padding:0;
  box-sizing:border-box;
  font-family: Arial, Helvetica, sans-serif;
}


.app{
  background-color: #88d4f6;
  height:100%;
}
#navbar {
  display: flex;
  align-items: center;
  padding: 2% 10%;
  justify-content: space-around;
}
#logo {
  width: 20%;
}
#logo > img {
  width: 100%;
  object-fit: contain;
}
.nav_links {
  display: flex;
  width: 50%;
  align-items: center;
  justify-content: center;
}

.nav_items {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 2rem;
  list-style: none;
}

.nav_items a {
  text-decoration: none;
  color: #235284;
  letter-spacing: 2;
  font-size: 0.9rem;
  transition: all 0.1s ease;
}
.nav_items a:hover {
  color: #000;
  font-weight: 500;
}
.icon > .list_icon_1 {
  font-size: 2.5rem;
  cursor: pointer;
  transition: all 0.1s ease;
}
.icon > .list_icon_1:hover {
  color: red;
}
.icon > .list_icon_2 {
  font-size: 3rem;
  cursor: pointer;
  transition: all 0.1s ease;
}
.icon > .list_icon_2:hover {
  color: #e8e8e9;
}
.icon > .list_icon_3 {
  font-size: 3rem;
  cursor: pointer;
  transition: all 0.1s ease;
}
.icon > .list_icon_3:hover {
  color: green;
}
.icon > .list_icon_4 {
  font-size: 3rem;
  transition: all 0.1s ease;
  cursor: pointer;
}
.icon > .list_icon_4:hover {
  color: #dfbb77;
}
.icon > .list_icon_5 {
  font-size: 3rem;
  cursor: pointer;
  transition: all 0.1s ease;
}
.icon > .list_icon_5:hover {
  color: blue;
}
.icon > .list_icon_6 {
  transition: all 0.1s ease;
  font-size: 3rem;
  cursor: pointer;
}
.icon > .list_icon_6:hover {
  color: #ccb49c;
}
.nav_icons {
  color: #235284;
  cursor: pointer;
}
.social_icon > .nav_icons {
  margin-left: 4px;
  color: #fff;
  font-size: 2rem;
  transition: all 0.1s ease;
}

.social_icon > .nav_icons:hover {
  color: #000;
  cursor: pointer;
}

.hamburger {
  cursor: pointer;
  color: #235284;
  display: none;
}
/* Responsive */

@media screen and (max-width: 1254px) {
  .nav_links {
    width: 60%;
  }
}

@media screen and (max-width: 1054px) {
  .hamburger {
    display: block;
  }
  .nav_links {
    display: none;
  }
  .social_icon {
    display: none;
  }
  .nav_links_mobile  {
    z-index: 3;
    background-color: #7dd1f6;
    position: absolute;
    top: 12%;
    left:0;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    transition: all 0.5s ease;
    width: 100%;
  }
  .nav_links_mobile .nav_items{
      margin-top:20px;
  }
  
  .nav_links_mobile .nav_items a{
      font-size: 1.4rem;
      cursor: pointer;
  }
  
  .social_icon_mobile {
    position: absolute;
    top: 15%;
    left:0;
    width: 100%;
    height: 100vh;
    z-index: 3;
    display: flex;
    justify-content: space-evenly;
  } 
  .social_icon_mobile > .nav_icons{
    margin-left: 4px;
    color: #fff;
    font-size: 3rem;
    transition: all 0.1s ease;
  }
  .social_icon_mobile > .nav_icons:hover{
      color:#000;
  }

}

.home{
    display: flex;
    justify-content: center;
    position: relative;
    align-items: center;
}

.intro{
    width:700px;
    position: relative;
}
.doremon{
    max-width:100%;
    border-radius: 1rem;
    object-fit: contain;
}

.intro >h3{
    position: absolute;
    font-size:3rem;
    left:2%;
    color:#7ba3e3;
    font-weight:300;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height: 1.5em;
    font-family: 'Chelsea Market', cursive;
}
.img_1{
    top:0%;
    left:0%;
    position: absolute;
    max-width: 100%;
    object-fit: contain;
}

.img_2{
    position: absolute;
    right:9%;
    max-width: 100%;
    object-fit: contain;
    top:0%;
}
.img_3{
    position: absolute;
    object-fit: contain;
    max-width:100%;
    top:90%;
    left: 2%;
}
.img_4{
    position: absolute;
    right:1%;
    max-width: 100%;
    object-fit: contain;
    top:55%;
}
.img_5{
    position: absolute;
    top:170%;
    left: 5%;
    max-width: 100%;
    object-fit: contain;
    
}
.img_6{
    position: absolute;
    right:1%;
    top:150%;
    max-width: 100%;
    object-fit: contain;
}




@media screen and (max-width:1309px) {
    intro{
        width:650px;
    }
    .img_1{
        left:-2%;
        max-width: 25%;
    }
    
    .img_2{
        right:5%;
        max-width: 25%;
    }
    .img_3{
        max-width:25%;
    }
    .img_4{
        right:2%;
        max-width: 25%;
        top:59%;
    }
    .img_5{
        position: absolute;
        top:170%;
        left: 5%;
        max-width: 100%;
        object-fit: contain;
        
    }
    .img_6{
        position: absolute;
        right:1%;
        top:150%;
        max-width: 100%;
        object-fit: contain;
    }
    
    
}
@media screen and (max-width:950px) {
    .intro{
        margin-top:1rem;
        width:660px;
    }
    .img_1{
        display: none;
    }
    
    .img_2{
        display: none;
    }
}
@media screen and (max-width:663px) {
    .intro{
        margin-top:2rem;
        width:550px;
    }
    .intro h3{
        font-size: 2.5rem;
    }
    .img_3{
        display: none;
    }
    
    .img_2{
        display: none;
    }
}
@media screen and (max-width:553px) {
    .intro{
        margin-top:2.5rem;
        width:500px;
    }
    .intro h3{
        font-size: 2.5rem;
    }
    .img_4{
        display: none;
    }
    
    .img_6{
        display: none;
    }
}
@media screen and (max-width:507px) {
    .intro{
        margin-top:2.5rem;
        width:400px;
    }
    .intro h3{
        font-size: 2rem;
    }
    
}

@media screen and (max-width:408px) {
    .intro{
        margin-top:3rem ;
        width:320px;
    }
    .intro h3{
        font-size: 1.5rem;
    }
    
    .img_5{
        display: none;
    }
}

@media screen and (max-width:324px) {
    .intro{
        width:280px;
    }
}

.show_top{
    display: flex;
    border-radius: 1rem; 
    align-items:flex-start;
    flex-direction: column;
    background-color: #fff;
}

.show_top >h3{
    color:#7ba3e3;
    font-family: 'Chelsea Market', cursive;
    font-size:1.1rem;
    margin-bottom: 0px;
    margin:0.4rem 0rem 0rem 0.4rem;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.show_top > p {
    color:#7ba3e3;
    font-family: 'Chelsea Market', cursive;
    font: size 0.8rem;
    padding:0.4rem;
    margin-bottom:1rem;
    letter-spacing: 2px;

}
.show_1 > img{
    max-width:100%;
    object-fit: contain;
    border-radius: 1rem;
}


@media screen and (max-width:310px) {
    .show_top >h3{
        font-size: 0.9rem;
    }
    .show_top >p{
        font-size: 0.7rem;
    }
    .show_1 >img{
        max-width: 80%;
    }
}



.showcase{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(270px,1fr));
    grid-gap: 2rem;
    padding:1.7rem 18%;
    margin-top:2rem;
}

@media screen and (max-width:430px) {
    .showcase{
        padding:1rem;
    }
}
@media screen and (max-width:324px) {
    .showcase{
        padding:0.5rem;
    }
}


.footer{
    padding:1rem;
}
.footer > P{
    text-align: center;
    margin:0.4rem;
    color:#7ba3e3;  
    font-size:0.8rem;
}
